.dhundo {
    display: flex;
    justify-content: center;
    align-items: center;

    margin: 20px 41px;
    height: 36px;
    background-color: white;
    color: #2A8CFF;

    border-radius: 33px;
}

.dhundo img {
    height: 16px;
    margin-right: 8px;
}

.dhundo h3 {
    height: fit-content;

    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.0025em;
}