.search {
    margin: 16px;
    gap: 10px;
    height: 48px;
    
    
    background: white;
    border-radius: 6px;
}

.search-box {
    display: flex;
    height: 16px;
    width: fit-content;
    margin: 16px;
    position: absolute;
}

.search-box input[type="text"] {
    padding: 0px;
    height: 16px;
    border: 0px solid white;
    width: 80vw;
}

.search-box button {
    background-color: white;
    border: 0px solid white;
}

.search-box img {
    height: 16px;
}