.results {
    margin: 10px;
}

.pagination {
    display: flex;
    justify-content: center;
    align-items: center;

    height: 48px;
    background-color: white;
    align-items: center;
    text-align: center;
    margin: 20px 9px;
    border-radius: 6px;
}

.pagination button {
    text-decoration: none;
    border: 0px solid white;
    background-color: white;
    display: flex;
}

.pagination img {
    fill: #5D6067;
    height: 20px;
    margin: auto;
    margin-left: 4px;
    margin-right: 4px;
}

.pagination h2 {
    height: 16px;
    font-style: normal;
    font-weight: 600;
    font-size: 14px;
    line-height: 16px;
}

.preview-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100%;
  background-color: #202124;
  display: flex;
  align-items: center;
  justify-content: center;
}

.preview-content {
  height: 277px;

  flex-direction: column;
  align-items: center;
  justify-content: center;
  
  justify-content: space-between;
}

.preview-thumbnail {
  width: 100%;
  height: 203px;
  margin-bottom: 16px;
}

.preview-thumbnail img {
  height: auto;
  width: 100vw;
}

.preview-info {
  height: 58px;
  margin-top: 16px;
  padding: 8px 24px;
}

.preview-info h2 {
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  height: fit-content;

  letter-spacing: 0.0025em;
  margin: 8px 0px;
  color: #FFFFFF;
}

.cv {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 0px;
  gap: 11px;
  
  width: fit-content;
  height: 14px;

  font-family: 'Barlow';
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;

  letter-spacing: 0.0025em;

  color: #9499A6;
}

p.pviews, p.pchannel {
  margin: 0px;
}

.preview-buttons {
  display: flex;
  padding-bottom: 16px;
  padding-left: 16px;
  gap: 10px;
  margin-top: auto;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0px;
  /* width: 100vw; */
}

.v-button {
  display: flex;
  padding: 12px 26px;
  border: 0px solid transparent;
  width: 80px;
  height: 38px;

  background: #2A8CFF;
  border-radius: 20px;

  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  align-items: center;
  text-align: center;
  letter-spacing: 0.0025em;

  color: #FFFFFF;

  position: absolute;
  bottom: 16px;
  left: 16px;
}

.c-button {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 12px 26px;
  gap: 10px;
  border: 0px solid transparent;

  width: 87px;
  height: 38px;

  background: #5D6067;
  border-radius: 20px;
  
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.0025em;

  color: #FFFFFF;

  position: absolute;
  bottom: 16px;
  right: 16px;
}