.result {
    position: relative;

    display: flex;
    background-color: white;
    padding: 8px;
    margin: 8px;
    border-radius: 6px;
}

.image-container {
    position: relative;
    display: inline-block;    
}

.image-container img {
    width: 132px;
    border-radius: 6px;
}
  
.text-overlay {
    position: absolute;
    bottom: 0px;
    right: 4px;
    color: white;
    border-radius: 2px;
}

.text-overlay p {
    height: 15px;
    font-size: 10px;
    /* margin: 4px; */
    padding: 0px 4px;
    background: rgba(0, 0, 0, 0.96);;
}

.info {
    position: relative;

    margin-left: 16px;
    margin-right: 8px;
    width: calc(100% - 132px);
    /* right: 8px; */
    padding: 0px;
}

.info h2 {
    font-size: 14px;
    top: 0px;
    margin-top: 0px;
    margin-bottom: 8px;
    height: fit-content;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.info p.channel {
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 10px;
    margin-top: 8px;
    letter-spacing: 0.0025em;

    color: #9499A6;
}

.views {
    position: absolute;
    bottom: 0;
    right: 0;

    height: 10px;
    width: fit-content;

    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 10px;
    text-align: right;
    letter-spacing: 0.0025em;

    color: #5D6067;
}